import React, {Component} from 'react';

class Scroll extends Component {
  constructor(props) {
    super(props);
    this.refElement = React.createRef();

    // Cette liaison est nécéssaire afin de permettre
    // l'utilisation de `this` dans la fonction de rappel.
    this.handleScroll = this.handleScroll.bind(this);
  }
  
  componentDidMount(){
		window.addEventListener('scroll', this.handleScroll);
  }
  handleScroll() {

    var elementRef = this.refElement;

    const scrollTop = elementRef.current.offsetTop; // position haute de l'élément
    const clientHeight = elementRef.current.clientHeight; // Longueur de l'élément

    const scrollY = window.scrollY;
    const windowsHeight = window.innerHeight;
    
    //console.log(` scrollTop: ${scrollTop} clientHeight ${clientHeight} scrollY: ${scrollY} windowsHeight: ${windowsHeight} `);
    //console.log(` ${scrollTop + clientHeight} < ${scrollY + windowsHeight} `);
    if (scrollY+windowsHeight>scrollTop+clientHeight*0.8){
      this.refElement.current.className='scrollOk';
      
    }
  }

  componentWillUnmount(){
    window.removeEventListener('scroll', this.handleScroll)
  }

  render() {
    return (
      <React.Fragment >
        <div
          className="scroll"
          ref = {this.refElement}>
          {this.props.children}
        </div>
      </React.Fragment>
    );
  }
} 

export default Scroll;